.ResourcePage {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.content {
  padding: 20px;
  flex-grow: 1;
}

h2 {
  margin-bottom: 20px;
}

/* Style for the header area above the table */
.table-header {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

.table-header button {
  padding: 10px 20px;
  cursor: pointer;
  background-color: darkblue;
  color: white;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

th {
  background-color: #f4f4f4;
}

button {
  padding: 5px 10px;
  cursor: pointer;
}

button.requested {
  background-color: #00cc66;
  color: white;
}

button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}
