/* ProjectPage.css */

.ProjectPage {
  padding: 20px;
  font-family: Arial, sans-serif;
}

h2 {
  margin-bottom: 20px;
}

.tabs {
  display: flex;
  margin-bottom: 20px;
}

.tabs button {
  padding: 10px 20px;
  cursor: pointer;
  background-color: darkblue;
  color: white;
  border: none;
  margin-right: 10px;
}

.tabs button.active {
  background-color: #0056b3;
}

.search-bar,
.create-form {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.search-bar input,
.create-form input {
  padding: 10px;
  border: 1px solid #ddd;
  width: 200px;
  margin-bottom: 10px;
}

.search-bar button,
.submit-btn {
  padding: 10px 20px;
  cursor: pointer;
  background-color: darkblue;
  color: white;
  border: none;
  align-self: flex-start;
}

.project-details {
  border: 1px solid #ddd;
  padding: 20px;
  margin-top: 20px;
}

.project-details p {
  margin: 10px 0;
}

.resource-btn {
  padding: 10px 20px;
  cursor: pointer;
  background-color: #00cc66;
  color: white;
  border: none;
  margin-top: 20px;
}

.create-project h3 {
  margin-bottom: 20px;
}

.create-form label {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.submit-btn {
  margin-top: 20px;
}
